@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&family=Mulish:wght@400;500&family=Open+Sans&family=Raleway:wght@500;800&display=swap);
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #000000;
    background: var(--black);
}

:root{
    --black: #000000;
    --white: #ffffff;
    --yellow: #f5d40d;
    --blue: #0C96B7;
    --raleway: 'Raleway', sans-serif;
    --mulish: 'Mulish', sans-serif;
    --opensans: 'Open Sans', sans-serif;
    --montserrat: 'Montserrat', sans-serif;
}
