.swiper-container {
    width: 100vw;
    height: -webkit-max-content;
    height: max-content;
    margin-top: 30px;
}

.swiper-wrapper {
    height: 100%;
    width: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.swiper-slide img{
